.friendMessage {
    color: white;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 47px;
}

.friendImageAndText {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    gap: 12px;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
}

.friendText {
    position: relative;
    max-width: 527px;
    width: fit-content;
    border-radius: 15px 15px 15px 0;
    background-color: rgb(245, 247, 251);
    padding: 7px 25px 8px 13px;
    box-shadow: 0 1px 2px rgba(29, 33, 38, 0.1);
}

.friendText:after {
    content: '';
    position: absolute;
    width: 12px;
    height: 12px;
    clip-path: polygon(100% 0, 0% 100%, 100% 100%);
    background-color: rgb(245, 247, 251);
    box-shadow: 0 1px 2px rgba(29, 33, 38, 0.1);
    left: -12px;
    bottom: 0;
}

.friendName {
    text-align: left;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 3px;
    color: rgb(0, 0, 0);
}

.friendMessageText {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    line-height: 20px;
    white-space: pre-wrap;
    color: rgb(0, 0, 0);

}

.friendTime {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    color: black;
    font-size: 10px;
    padding-left: 10px;
}
