.message {
    color: white;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 47px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    gap: 12px;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
}

.text {
    position: relative;
    max-width: 527px;
    width: fit-content;
    border-radius: 15px 15px 0 15px;
    background-color: rgb(0, 102, 204);
    padding: 7px 25px 8px 13px;
    /*word-wrap: break-word;*/
}


.text:after {
    content: '';
    position: absolute;
    width: 12px;
    height: 12px;
    clip-path: polygon(0 0, 0% 100%, 100% 100%);
    background-color: rgb(0, 102, 204);
    right: -12px;
    bottom: 0;
}

.name {
    text-align: right;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 3px;
}

.messageText {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    line-height: 20px;
    white-space: pre-wrap;
}

.time {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    color: black;
    font-size: 10px;
    padding-right: 10px;
}

