.sendForm {
    display: flex;
    gap: 44px;
    align-items: center;
}

.textarea {
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    width: 100%;
    height: 40px;
    background-color: rgb(245, 247, 251);
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    padding: 12px 24px;
    overflow: hidden;
    outline: none;
    border: none;
    resize: none
}

.button {
    width: 120px;
    height: 40px;
    border-radius: 20px;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: white;
    background-color: rgb(0, 102, 204);
    outline: none;
    border: none;
    cursor: pointer;
}

